#slash{
	display: none;
}
.btn {
    color: #fff;
    background-color: #0069d9;
    border-color: #0062cc;
}

.login-admin {
    background-color: #fff;
}

.form-control {
    width: 100% !important;
}
:root {
    --text-color: #464646;
    --white-color: #fff;
    --black-color: #000;

    --header-height: 100px;
    --campaign-height: 660px;
    --nav-footer: 62px;
    --cart-productList-item: 162px;
    --search-item-height: 130px;
}



* {
    padding: 0;
    margin: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    /* font-family: 'Lato', 'Arial' ,sans-serif; */
    font-family: 'Montserrat', '-apple-system', 'BlinkMacSystemFont', sans-serif;;
    box-sizing: border-box;
    text-rendering: optimizeLegibility;
}

.col-1 {
    width: 100%;
    max-width: 25%;
    padding: 0;
    flex: 1 1;
}

.col-2 {
    width: 100%;
    max-width: 50%;
    padding: 0;
    flex: 1 1;
}

.category-male .col-1,
.contact-info .col-1 {
    width: 100%;
    max-width: 30%;
}

.category-male .col-1 {
    width: 100%;
    max-width: 32%;
}

.contact-info .col-2 {
    width: 100%;
    max-width: 35%;
}

.btn {
    width: 270px;
    padding: 15px;
    font-size: 1.4rem;
    text-transform: uppercase;
    background-color: #efefef;
    color: #000;
    color: var(--black-color);
    border: 1px solid #000;
}

.btn-l {
    width: 270px;
    height: 70px;
    font-size: 1.4rem;
    text-transform: uppercase;
}

.footer-bottom .col-1 {
    width: 100%;
    max-width: 50%;
}

.uppercase {
    text-transform: uppercase;
}


.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    padding-left: -4px;
    padding-right: -4px;
}

.row.no-gutters {
    padding-left: 0;
    padding-right: 0;
}

/* Mobile */
.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}

.c-0 {
    display: none;
}

.c-1 {
    flex: 0 0 8.33333%;
    max-width: 8.33333%;
}

.c-2 {
    flex: 0 0 16.66667%;
