    align-items: center;
    height: 68px;
    top: var(--header-height);
    left: 0;
    right: 0;
    background-color: #f5f5f5;
    padding: 0 60px;

}


.search-body__input {
    background-color: transparent;
    border: none;
    font-size: 1.7rem;
    outline: none;
    color: #656565;
}

.search-body-notify {
    padding: 0 60px;
    height: 32px;
    display: flex;
    flex-direction: column-reverse;

}

.search-body-notify-text {
    font-size: 1.5rem;
    border-bottom: 1px solid #ccc;
}

.search-body-productList {
    padding: 0 60px;
    max-height: calc(var(--search-item-height)*4);
    overflow-y: scroll;
}

.search-body-item {
    display: flex;
    justify-content: space-between;
    height: var(--search-item-height);
}

.search-body-item__pictureName {
    display: flex;
    align-items: center;
}

.search-body-item__img {
    width: 60px;
    margin: 20px;
}

.search-body-item__price {
    display: flex;
    align-items: center;
}

.header-right__icon-bag {
    /* display: inline-block; */
    position: relative;
    font-size: 1.8rem;
    top: -2px;
    margin-left: 15px;
}

.cart-quantity {
    position: absolute;
    right: -22px;
    top: 0;
    width: 20px;
    height: 20px;
    font-size: 1.3rem;
    text-align: center;
    background-color: var(--black-color);
    color: var(--white-color);
    line-height: 20px;
    border-radius: 50%;
}

.header-right__icon-search {
    position: relative;
    font-size: 2.2rem;
    top: -1px;
}

.header-right__item {
    position: relative;
    padding: 12px;
}

.header-right__account::after {
    content: "";
    position: absolute;
    left: -57px;
    right: -38px;
    bottom: -42px;
    height: 44px;
}

.header-right__account:hover .header-right__account-text {
    border-bottom: 1px solid #000;
    cursor: pointer;
}

.header-right__item:hover .header-account {
    display: block;
}

.header-account {
    display: block;
    position: absolute;
    list-style: none;
    background-color: #fff;
    right: -50%;
    width: 170px;
    top: calc(100% + 32px);
    padding: 29px 25px;
    display: none;
}

.header-account::before {
    content: "";
    position: absolute;
    border-width: 5px 7px;
    border-style: solid;
    border-color: transparent transparent #000 transparent;
    box-shadow: 0px 0px 20px rgb(0 0 0 / 10%);
    top: -9px;
    right: 55%;
}

.nav-account__item {
}

.nav-account__item:first-child  {
    border-bottom: 1px solid #ccc;
}

.nav-account__item a {
    color: var(--black-color);
}

.nav-account__link-logout,
.nav-account__link-info,
.nav-account__link-login,
.nav-account__link-register {
    display: inline-block;
    padding: 10px 0;
    text-decoration: none;
    color: var(--text-color);
    font-size: 1.4rem;
    cursor: pointer;
}

/* Banner */
.banner {
    position: relative;
    /* width: 100%; */
    margin-top: var(--header-height);
}

.banner-des { 
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding-left: 48px;
}

.banner-category-heading ,
.banner-des__heading {
    line-height: 4.6rem;
    font-size: 3.8rem;
    color: var(--white-color);
    font-weight: 500;
    word-spacing: 4px;
    letter-spacing: 1px;
}

.banner-des__sub-heading {
    margin-top: 16px;
}

.banner-category-subheading,
.banner-des__text{
    color: var(--white-color);
    font-size: 1.6rem;
    line-height: 2.2rem;
    font-weight: 300;
}

.banner-des__link {
    display: inline-block;
    margin-top: 40px;
    color: var(--white-color);
    font-size: 1.8rem;
    padding: 22px 90px;
    text-decoration: none;
    border: 1px solid var(--white-color);
}

.banner-des__link:hover {
    color: var(--white-color);
}

.banner-img {
    width: 100%;
    object-fit: contain;
}

@-webkit-keyframes fadeIn {
    from{
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from{
        opacity: 0;
    }
    to {
        opacity: 1;
