/* Check out */

.checkout {
    margin-top: var(--header-height);
    padding: 35px 42px 120px 42px;
}

.checkout-body {
    display: flex;
}

.checkout-order {
    width: 400px;
}

.checkout-info {
    padding: 24px;
}

.checkout-order__heading {
    font-size: 1.8rem;
    letter-spacing: 1px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 12px;
    color: var(--black-color);
}

.checkout-order__note {
    font-size: 1.4rem;
    margin: 12px 0;
    line-height: 1.4;
    color: var(--black-color);
}

.checkout-order__note span {
    font-weight: 600;
    color: #3eb204;
}

.checkout-form {
    padding: 16px 0;
}

.form-group{
    margin-bottom: 16px;
}

.form__input{
    min-height: 50px;
    width: 562px;
    font-size: 1.8rem;
    padding: 15px;
    border: 1px solid #e5e5e5;
}

.checkout-pay {
    margin: 32px 0 16px 0;
}

.checkout-pay-heading {
    font-size: 1.4rem;
    text-transform: uppercase;
    line-height: 1.4;
    color: var(--black-color);
    letter-spacing: 0.6px;
}

.checkout-pay-select {
    margin-top: 20px;
    display: flex;
}

.checkout-pay-option {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    padding: 30px 20px;
    border: 1px solid #ccc;
    background-color: #f5f5f5;
}

.checkout-pay-option:first-child {
    margin-right: 16px;
}

.checkout-pay-left {

}

.checkout-pay-left-icon {
    font-size: 1.4rem;
    margin-left: 5px;
}

.checkout-pay__heading {
    line-height: 1.6;
    font-size: 1.4rem;
    color: var(--black-color);
}

.checkout-pay-subheading {
    font-size: 1.3rem;
    color: var(--black-color);
}

.checkout-pay-right {

}

.checkout-pay-right-icon {
    font-size: 1.6rem;
    font-weight: 900;
    margin-top: 5px;
    display: inline-block;
}

.checkout-pay-right-img {
    width: 40px;
}

.checkout-btn {
    display: block;
    width: 75%;
    margin: 0 auto;
    min-height: 50px;
    text-transform: uppercase;
    font-size: 1.4rem;
    color: #fff;
    border: none;
    background-color: #3bb100;
}

.checkout-note {
    font-size: 1.4rem;
    letter-spacing: 0.6px;
    margin-bottom: 22px;
    color: var(--black-color);
}

.checkout-hotline {
    font-size: 1.6rem;
    font-weight: 100;
    color: var(--black-color);
}

.checkout-order{
    box-shadow: 0px 0px 40px rgb(0 0 0 / 10%);
    background-color: #fff;
    padding: 32px;
    flex: 1 1;
}

.checkout-order-wrap{

}

.order-heading{
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 100;
    letter-spacing: 0.8px;
    margin-bottom: 28px;
    color: var(--black-color);
}

.order-heading__edit {
    font-size: 1.4rem;
    float: right;
    font-weight: 100;
    text-decoration: underline;
    line-height: 1.5rem;
    color: var(--black-color);
}

.order-subheading {
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: 100;
    letter-spacing: 0.8px;
    color: var(--black-color);
    padding-bottom: 16px;
    border-bottom: 1px solid #ccc;
}

.order-subheading__price {
    float: right;
    font-weight: 100;
    line-height: 1.5rem;
}

.order-listProduct{

}

.order-item {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-bottom: 1px solid #ccc;
}

.order-item-wrap {
    display: flex;
}

.order-item-picture {

}

.order-item-img {
    width: 70px;
}

.order-item-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 8px;
}

.order-item-name {
    font-size: 1.4rem;
    color: #2c2c2c;
    line-height: 1.4;
}

.order-item-cate {
    font-size: 1.4rem;
    color: #aaaaaa;
    line-height: 1.4;
}

.order-item-quantity {
    font-size: 1.4rem;
    color: var(--black-color);
    line-height: 1.4;
}

.order-item-price {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.order-item-price-current {
    line-height: 1.4;
    color: var(--black-color);
    font-size: 1.6rem;
    opacity: 0.7;
    letter-spacing: 0.8px;
}

.order-item-price-old {
    font-size: 1.3rem;
    color: var(--black-color);
    opacity: 0.5;
    text-decoration: line-through;
    text-align: right;
    letter-spacing: 0.8px;
}

.order-item-price-current-unit {
    font-size: 1.8rem;
    text-decoration: underline;
    margin-left: 4px;
}

.order-item-price-old-unit {
    font-size: 1.4rem;
}

.order-coupon {
    padding: 30px 0;
    border-bottom: 1px solid #ccc;
}

.order-coupon-message {
    font-size: 1.5rem;
    color: #f33a58;
    margin-top: 10px;
    margin-bottom: 0;
}

.order-coupon-input {
    min-height: 50px;
    /* max-width: 223px; */
    width: 223px;
    padding: 15px;
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: 1px;
}

.order-coupon-btn {
    min-height: 50px;
    padding: 0 16px;
    text-transform: uppercase;
    font-weight: 600;
    color: #444;
    opacity: 0.9;
    margin: 0;
    cursor: pointer;
    float: right;
    border: 1px solid var(--black-color);
}

.order-coupon-detail,
.order-sub-total {
    padding: 30px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
}

.order-coupon-detail__text,
.order-sub-total__text {
    font-size: 1.5rem;
    opacity: 0.4;
}

.order-coupon-detail__price,
.order-sub-total__price {
    font-size: 1.7rem;
    color: var(--black-color);

}

.order-coupon-detail__unit,
.order-sub-total__unit {
    text-decoration: underline;
    font-size: 1.8rem;
}

.order-total {
    display: flex;
    justify-content: space-between;
    margin: 38px 0 10px 0;
}

.order-total__text {
    font-size: 1.4rem;
    text-transform: uppercase;
    line-height: 2;
    color: var(--black-color);
}

.order-total__price {
    font-size: 2rem;
    color: var(--black-color);
}

.order-total__unit {
    font-size: 2.1rem;
    text-decoration: underline;
}

.order-note {
    font-size: 1.2rem;
    text-transform: uppercase;
    opacity: 0.5;
}


/* End check out */

.confirm {
    margin-top: var(--header-height);
    height: 300px;
}

.confirm-heading {
    margin-top: 250px;
    text-align: center;
    font-size: 25px;
    font-weight: 100;
    color: rgb(129, 6, 6);
}

.active {
    display: block;
}


/* Login */

.login {
    margin-top: var(--header-height);
    padding: 140px 0;
    display: flex;
}

.login-left {
    width: 685px;
    padding: 70px 108px;
    box-shadow: 0px 0px 40px rgb(0 0 0 / 10%);
}

.login-form {

}

.form-heading {
    font-size: 3rem;
    font-weight: 100;
    text-align: center;
    letter-spacing: 1.5px;
    margin-bottom: 10px;
    color: var(--black-color);
}

.form-subheading {
    font-size: 1.3rem;
    text-align: center;
    line-height: 1.4;
    color: #818181
}

.form-input {
    margin-top: 35px;
}

.input-group {
    display: block;
    width: 100%;
    padding: 15px 8px;
    font-size: 1.4rem;
    border: 1px solid #ccc;
    letter-spacing: 0.7px;
    margin-bottom: 30px;
}

.login-btn {
    display: block;
    width: 100%;
    background-color: #000;
    font-size: 1.5rem;
    text-transform: uppercase;
    color: var(--white-color);
    padding: 8px 0;
}

.login-right {
    width: calc(1200px - 685px);
    padding: 80px 50px 60px;
    background-color:#ccc;
    opacity: 0.7;
}

.new-customer {

}

.new-customer-heading {
    font-size: 3rem;
    font-weight: 100;
    text-align: left;
    letter-spacing: 1.5px;
    margin-bottom: 8px;
    color: var(--black-color);
}

.new-customer-sub-heading {
    font-size: 1.4rem;
    letter-spacing: 0.5px;
    margin-bottom: 30px;
    color: #3b3b3b
}

.benefit {
    margin-bottom: 40px;
}


.benefit-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.benefit-item span {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #000;
    display: inline-block;
    margin-right: 15px;
}

.benefit-item__text {
    font-size: 1.3rem;
    letter-spacing: 0.3px;
    color: var(--black-color);
}

.new-customer-link {
    text-align: center;
    display: block;
    text-decoration: none;
    width: 100%;
    font-size: 1.4rem;
    text-transform: uppercase;
    border: 1px solid #000;
    color: var(--black-color);
    background-color: var(--white-color);
    padding: 10px 0;
    letter-spacing: 0.6px;
}



/* End Login */

/* Register */
.register {
    margin-top: calc(var(--header-height) + 140px);
    margin-bottom: 140px;
}

.register-content {
    width: 685px;
    margin: auto;
    padding: 70px 108px;
    box-shadow: 0px 0px 40px rgb(0 0 0 / 10%);
}

.register-form {

}

.register-heading {
    font-size: 3rem;
    font-weight: 100;
    text-align: center;
    letter-spacing: 1.5px;
    margin-bottom: 10px;
    color: var(--black-color);
}

.register-sub-heading {
    font-size: 1.3rem;
    text-align: center;
    line-height: 1.4;
    color: #818181;
    margin-bottom: 3px;
}

.register-wrap {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}

.register-note {
    font-size: 1.2rem;
    text-decoration: underline;
    font-style: italic;
    display: inline-block;
    text-align: center;
    margin: auto;
    line-height: 1.6;
    color: var(--black-color);
}

.register-group {

}

.register-btn {
    display: block;
    width: 100%;
    background-color: #000;
    font-size: 1.5rem;
